
.settings{
    background-color: rgb(255, 0, 183);
    color: black;
    position: absolute;
    width:5%;
    min-width: 5%;
    height: 11.5%;
    right: 20%;
    top:5%;
    display: flex;
    flex-direction: column;
    align-items: center;
    transition: opacity 2s,z-index 2s;
}
.true{
    z-index: 1;
    opacity: 0.8;
}

.false{
    opacity: 0;
    z-index: -1;
}

label{
    text-align: center;
    font-size: 15px;
    margin: 0.5em;
}